import * as axios from "axios";

import { config } from "../../server/config";
import { UserObject } from "./user.obj";

export class UserAPIRequest {

	public static registerUser = (user: UserObject, pw: string) => {
		return fetch(config.url + "/api/v1/user/register", {
			body: JSON.stringify({
				name: user.getName(),
				sid: user.getSid(),
				password: pw,
				role: user.getRole(),
			}),
			method: "POST",
			headers: {
				"content-type": "application/json",
			},
			mode: "cors",
		});
	}

	public static loginUser = (id: string, pw: string) => {
		// return fetch(config.url + "/api/v1/auth/login", {
		// 	body: JSON.stringify({
		// 		id,
		// 		pw,
		// 	}),
		// 	method: "POST",
		// 	headers: {
		// 		"content-type": "application/json",
		// 	},
		// 	credentials: "include",
		// });
		return axios.default.post(
			config.url + "/api/v1/auth/login",
			{
				id,
				pw,
			},
		);
	}

	public static logoutUser = () => {
		// return fetch(config.url + "/api/v1/auth/logout");
		return axios.default.get(config.url + "/api/v1/auth/logout");
	}

	public static checkSignedIn = () => {
		// return fetch(config.url + "/api/v1/auth/check");
		return axios.default.get(config.url + "/api/v1/auth/check");
	}
}
